'use client'

import { useTransition } from "react"
import type { BookFormState } from "@/app/actions/book-actions"
import { createBook, updateBook } from "@/app/actions/book-actions"
import { BookConfigLayout } from "./BookConfigLayout"
import { GeneralSection } from "./GeneralSection"
import { PeriodSection } from "./PeriodSection"
import { VisualSection } from "./VisualSection"
import { NotificationSection } from "./NotificationSection"
import { DangerZone } from "./DangerZone"
import { Save } from "lucide-react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { useFinance } from "@/context/FinanceContext"

interface BookFormProps {
    mode: 'create' | 'edit'
    defaultValues?: any
    bookId?: string
}

export function BookForm({ mode, defaultValues, bookId }: BookFormProps) {
    const { refreshBooks, activateLedger } = useFinance()
    const router = useRouter()
    const [isPending, startTransition] = useTransition()

    const handleSubmit = (formData: FormData) => {
        startTransition(async () => {
            const state: BookFormState = {}
            if (mode === 'create') {
                const result = await createBook(state, formData)
                if (result?.data) {
                    await refreshBooks()
                    activateLedger(result.data.id)
                    router.push('/ledgers')
                } else if (result?.message) {
                    alert(result.message)
                }
            } else if (bookId) {
                const result = await updateBook(bookId, state, formData)
                // Update actions usually redirect server-side, but if we changed it:
                // Current updateBook redirects to settings. Ideally should redirect to ledgers?
                // User only asked for Create to go to Ledgers? 
                // "al dar clic a la pagina de cancelar debe redirigirme a la pagina de libros contables" -> Done.
                // "al intentar crear un libro contable ... tiene que crear el libro y redirigirme a la pagina de libros contables" -> Done.
                // Edit flow wasn't explicitly requested to change, but Cancel was.
                // Let's leave Edit as is (redirects to settings per server action).
                // But wait, updateBook returns { message: ... } on success if it doesn't redirect?
                // app/actions/book-actions.ts: lines 98-99: revalidatePath -> return message.
                // It does NOT redirect! It returns message.
                // So for Edit, we should probably stay on page or redirect?
                // If it returns message, we might want to alert success?
                if (result?.message && !result.message.includes('Error')) {
                    alert('Libro actualizado correctamente')
                    router.refresh()
                } else if (result?.message) {
                    alert(result.message)
                }
            }
        })
    }

    return (
        <BookConfigLayout>
            <form action={handleSubmit} className="space-y-6">
                <div className="flex items-center justify-between bg-white dark:bg-background-dark/50 backdrop-blur-md sticky top-0 z-10 p-4 -mx-4 sm:mx-0 rounded-xl border border-gray-100 dark:border-white/5 shadow-sm">
                    <div className="text-sm text-text-muted hidden sm:block">
                        {mode === 'edit' ? `Editando: ${defaultValues?.name}` : 'Nuevo Libro'}
                    </div>
                    <div className="flex items-center gap-3 w-full sm:w-auto justify-end">
                        <Link
                            href="/ledgers"
                            className="px-4 py-2 text-sm font-medium text-text-main bg-white dark:bg-white/5 border border-gray-200 dark:border-white/10 rounded-lg hover:bg-gray-50 dark:hover:bg-white/10 transition-colors"
                        >
                            Cancelar
                        </Link>
                        <button
                            type="submit"
                            disabled={isPending}
                            className="px-4 py-2 text-sm font-medium text-white bg-primary hover:bg-primary-hover rounded-lg shadow-lg shadow-primary/25 flex items-center gap-2 transition-all disabled:opacity-70 disabled:cursor-not-allowed"
                        >
                            {isPending ? (
                                <div className="h-4 w-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                            ) : (
                                <Save size={16} />
                            )}
                            {mode === 'create' ? 'Crear Libro' : 'Guardar Cambios'}
                        </button>
                    </div>
                </div>

                <GeneralSection defaultValues={defaultValues} />
                <PeriodSection defaultValues={defaultValues} />
                <VisualSection defaultValues={defaultValues} />
                <NotificationSection defaultValues={defaultValues} />
            </form>

            {mode === 'edit' && (
                <div className="pt-6 border-t border-gray-100 dark:border-white/5">
                    <DangerZone bookId={bookId} />
                </div>
            )}
        </BookConfigLayout>
    )
}
